import React from "react";

class Card extends React.Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className="col s12 m2">
        <blockquote>{this.props.title}</blockquote>
        <p>
          {this.props.text}
        </p>
      </div>
    );
  }
}

export default Card;
