import React from "react";
import data from "./data.json";
import Card from "./Card";

class Field extends React.Component {
  constructor(props) {
    super(props);
  }
  componentDidMount(){
    
  }
  render() {
    return (
      <div>
        {data.map((elem)=>{
            return <Card title={elem.title} text={elem.text}/>
        })}
      </div>
    );
  }
}

export default Field;
